import { ConnectionQuality } from '@yunzhoukit/yunzhou-client';

export namespace System {
  export const DEVICE_AUDIOOUTPUT = 'audiooutput'; // 

  export const DEVICE_AUDIOINPUT = 'audioinput'; // 

  export const DEVICE_VIDEOINPUT = 'videoinput'; // 

  export const DEVICE_DEFAULT_DEVICE_ID = 'default'; // id

  export const DEVICE_DEFAULT_COMMUNICTIONS_ID = 'communications'; // id

  export type Devices = Partial<{
    audioinputs: MediaDeviceInfo[]; //  
    audiooutputs: MediaDeviceInfo[]; //  
    videoinputs: MediaDeviceInfo[]; // 
  }> | null;

  export const NETWORK_STATUS_ONLINE = 'online';
  export const NETWORK_STATUS_OFFLINE = 'offline';

  // 
  export type NetworkStatus = typeof NETWORK_STATUS_ONLINE | typeof NETWORK_STATUS_OFFLINE;

  // 
  export type NetworkQuality =
    | typeof ConnectionQuality.Excellent
    | typeof ConnectionQuality.Good
    | typeof ConnectionQuality.Poor
    | typeof ConnectionQuality.Unknown;

  // 
  export type Network = Partial<{
    status: NetworkStatus;
    quality: NetworkQuality;
  }> | null;
}
